import React, { Component } from 'react'

export default class Lozung extends Component {
    render() {
        return (
            <div className='col black-back d-flex align-items-center  '>
                <div className='container'  >
                    <div className='lozung '>Чистота<br />
                        <span className='color2'>в центре</span><br />
                        внимания</div>
                </div>
            </div>
        )
    }
}



