import React, {Component} from 'react'
import LogoImage from '../../images/logo.webp'
import './Logo.css'
export default class Logo extends Component {
    render() {
        return (
            <div className='col' >
                <img src={LogoImage} className='img-fluid' alt='logo'></img>
            </div>
        )
    }
}



