import React, {Component} from 'react'
import './BlackBlock.css'
export class BlackBlock extends Component {
    render({
        ...res
    }) {
        return (
            <div></div>
        )
    }
}
export default({
    width,
    height,
    textTop,
    textLeft,
    textWidth,
    ...res
}) => {
    return (
        <div
            className='black-back'
            style={{
            width,
            height
        }}>
            <div
                className='conatiner'
                style={{
                left: textLeft,
                top: textTop,
                width: textWidth,
                position: 'absolute'
            }}>
                <div className='txtNew' {...res}/>

            </div>
        </div>
    )
}


