import React, {Component} from 'react'
import PropTypes from 'prop-types'

import './PhotoSection.css'

export default class PhotoSection extends Component {
    render() {
        const {
            type,
            what,
            auto,
            srcDo,
            srcAfter,
            isOnePic
        } = this.props.val
        return (
            <div className='row pt-5'>
                <div className='col-5 black-back d-flex align-items-center '>
                    <div className='conatiner w-100 h-100'>
                        <div className='nadpis pt-5'>
                            {type}
                            <br/>
                            <span className='color2 pt-3'>{what}
                            </span><br/> {auto}
                        </div>
                    </div>
                </div>

                <div className='col m-3'>
                    <img src={srcDo} className='rounded img-fluid' alt=''></img>
                </div>
                {!isOnePic
                    ? <div className='col m-3'>
                            <img src={srcAfter} className='rounded img-fluid' alt=''></img>
                        </div>
                    : null}

            </div>
        )
    }
}
PhotoSection.propType = {
    val: PropTypes
        .shape({
        type: PropTypes.string.isRequired,
        what: PropTypes.string.isRequired,
        auto: PropTypes.string.isRequired,
        srcDo: PropTypes.string.isRequired,
        srcAfter: PropTypes.string,
        isOnePic: PropTypes.bool.isRequired
    })
        .isRequired
}



